export const createLinkHandler = () => {
  let lastRedirectAttempt = 0;
  const REDIRECT_COOLDOWN = 1000; // 1 
  let isFirstAttempt = true;

  const delay = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

  const openUrlWithFallback = async (url) => {
    if (!url) return false;

    const now = Date.now();
    if (now - lastRedirectAttempt < REDIRECT_COOLDOWN) {
      return false;
    }
    lastRedirectAttempt = now;

    // 
    const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);

    // 1.  window.open 
    const tryWindowOpen = async () => {
      return new Promise(async (resolve) => {
        let isOpened = false;
        const onBlur = () => {
          if (!isOpened) {
            isOpened = true;
            window.removeEventListener("blur", onBlur);
          }
        };
        window.addEventListener("blur", onBlur);

        try {
          // 
          if (isMobile && isFirstAttempt) {
            const link = document.createElement("a");
            link.href = url;
            link.target = "_blank";
            link.rel = "noopener noreferrer";

            // 
            const clickHandler = (e) => {
              e.preventDefault();
              window.removeEventListener("blur", onBlur);

              // setTimeout
              setTimeout(() => {
                const newWindow = window.open(
                  url,
                  "_blank",
                  "noopener,noreferrer"
                );
                isOpened = !!newWindow;
                resolve(isOpened);
              }, 50);
            };

            link.addEventListener("click", clickHandler);
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            isFirstAttempt = false;
          } else {
            // 
            await delay(300);
            const newWindow = window.open(url, "_blank", "noopener,noreferrer");

            setTimeout(() => {
              if (!isOpened) {
                window.removeEventListener("blur", onBlur);
              }
              resolve(!!newWindow);
            }, 1000);
          }
        } catch (e) {
          console.log("window.open :", e);
          window.removeEventListener("blur", onBlur);
          resolve(false);
        }
      });
    };

    // 2.  Anchor Tag 
    const tryAnchorTag = async () => {
      return new Promise(async (resolve) => {
        let isNavigated = false;
        const onBlur = () => {
          if (!isNavigated) {
            isNavigated = true;
            window.removeEventListener("blur", onBlur);
          }
        };
        window.addEventListener("blur", onBlur);

        try {
          await delay(300);
          const link = document.createElement("a");
          link.href = url;
          link.target = "_blank";
          link.rel = "noopener noreferrer";

          // 
          if (isMobile) {
            link.setAttribute("data-mobile-redirect", "true");
          }

          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);

          setTimeout(() => {
            if (!isNavigated) {
              window.removeEventListener("blur", onBlur);
            }
            resolve(isNavigated);
          }, 1000);
        } catch (e) {
          console.log("Anchor Tag :", e);
          window.removeEventListener("blur", onBlur);
          resolve(false);
        }
      });
    };

    //  window.open
    let success = await tryWindowOpen();
    if (success) return true;

    // // 
    // if (isMobile && isFirstAttempt) {
    //   await delay(100);
    //   success = await tryAnchorTag();
    //   if (success) return true;
    // }

    return false;
  };

  return {
    openUrlWithFallback,
  };
};
