import React, { useState } from "react";
import Activity from "./Activity";
import { Shopee, Momo } from "./Bulletin";

function TabContent({ brand, activities, isActive }) {
  const [activeActivity, setActiveActivity] = useState(
    Object.keys(activities)[0]
  );
  return isActive ? (
    <div>
      <div className="subtabs">
        {Object.keys(activities).map((activity) => (
          <button
            key={activity}
            className={`subtablinks ${
              activeActivity === activity ? "active" : ""
            }`}
            onClick={() => setActiveActivity(activity)}
          >
            {activity}
          </button>
        ))}
      </div>
      <div className="bulletin-board">
        {activeActivity === "" && <Shopee />}
        {activeActivity === "" && <Shopee />}
        {activeActivity === "-3C" && <Shopee />}
        {activeActivity === "-" && <Shopee />}
        {activeActivity === "Mo" && <Momo />}
        {activeActivity === "-" && <Momo />}
      </div>
      {Object.keys(activities).map((activity) => (
        <Activity
          key={activity}
          brand={brand}
          activity={activities[activity]}
          isActive={activity === activeActivity}
        />
      ))}
    </div>
  ) : null;
}

export default TabContent;
